<template>
	<div>
		<Nav navtitle=''></Nav>
		<div class="midbottom">
      <p class="title"></p>
      <p class="ptitle">  </p>
			<p class="midstp"></p>
			<p class="ptitle"> </p>
      <p class="midstp"></p>
      <p class="ptitle"> </p>
      <p class="midstp"></p>
		</div>
	</div>
</template>

<script>
import Nav from '../../common/nav.vue'
export default {
  components: {
    Nav
  }
}
</script>

<style scoped>
p {
  margin: 0;
  padding: 0;
}
.midbottom {
  width: 100%;
  padding-bottom: 15px;
}
.midstp {
  margin-top: 5px;
  font-size: 10px;
  color: #666666;
  line-height: 16px;
  padding: 0 15px;
  text-indent: 2em;
}
.title{
  font-size: 14px;
  margin-top: 30px;
  font-weight: 600;
  text-align: center;
  color: #f1662c;
}
.ptitle{
  font-size: 14px;
  margin-top: 10px;
  font-weight: 600;
  padding: 0 15px;
}
</style>



